import Controls from './Controls';


export default {
  components: {
    controls: Controls,
  },

  data() {
    return {
      snapshots: [],
      cursor: 0,
      isPaused: true,
      playInterval: null,
    };
  },

  created() {
    this.snapshots.push(this.firstSnapshot());
    const algo = this.algorithm();
    for (;;) {
      const res = algo.next();
      if (res.done) {
        break;
      }

      this.snapshots.push(res.value);
    }
  },

  mounted() {
    this.initView();
    this.update(this.snapshots[this.cursor]);
  },

  methods: {
    * algorithm() {
      yield {};
    },

    initView() {
    },

    update(dataIgnored) {
    },

    firstSnapshot() {
      return {};
    },

    togglePlayPause() {
      this.isPaused = !this.isPaused;

      if (this.isPaused) {
        clearInterval(this.playInterval);
      } else {
        const self = this;
        this.playInterval = setInterval(() => {
          if (self.cursor < self.snapshots.length - 1) {
            self.cursor += 1;
            self.update(self.snapshots[self.cursor]);
          } else {
            self.isPaused = true;
            clearInterval(self.playInterval);
          }
        }, 500);
      }
    },

    changeSnapshot(step) {
      if (!this.isPaused) {
        this.togglePlayPause();
      }

      const newCursor = this.cursor + step;
      if (newCursor >= 0 && newCursor < this.snapshots.length) {
        this.cursor = newCursor;
        this.update(this.snapshots[newCursor]);
      }
    },
  },
};



